  position: relative;
  display: inline-block;
  width: 50px;
  height: 22px;
  user-select: none;
}
.switch input[data-v-081c7cb9] {
  opacity: 0;
  width: 0;
  height: 0;
}
.switch .slider[data-v-081c7cb9] {
  user-select: none;
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 22px;
}
.switch .slider > div[data-v-081c7cb9] {
  position: absolute;
  height: 18px;
  width: 18px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}
input:checked + .slider[data-v-081c7cb9] {
  background-color: #2196F3;
}
input:checked + .slider > div[data-v-081c7cb9] {
  transform: translateX(26px);
}
